const ChargesMap = ({}) => {
    const styles = useStyles();
    // useEffect(() => {
    //     getChargers()
    // }, [])
    const [current, setCurrent] = useState(null)
    const handleSetCurrent = (charge) => {
        setCurrent(charge)
    }
    return (
        <Grid container>
            <Grid style={{ display: 'flex', justifyContent: 'center' }} xs={current ? 3 : 0}>
                {
                    current &&
                    <div className={styles.list}>
                        <Typography className={styles.companyTitle}>{current.company}</Typography>
                        <Typography className={styles.companyProperty}>{current.title}</Typography>
                        <Typography className={styles.companyProperty}>{current.description}</Typography>
                        <Typography className={styles.coordinate}>{current.latitude}, {current.longitude}</Typography>
                        <Typography className={styles.price}>{current.pricePerMinute} .  </Typography>
                        <Typography className={styles.price}>  : ~{current.pricePerMinute * 30} .</Typography>
                        <div className={styles.actions}>
                            <Button fullWidth variant={'contained'} color={'secondary'} size={'large'}>START</Button>
                        </div>
                    </div>
                }
            </Grid>
            <Grid xs={current ? 9 : 12}>
                <YMaps>
                    <div style={{position: 'relative'}}>
                        <Map width={'100%'} height={window.innerHeight * 0.8} defaultState={{center: [43.237793, 76.945690], zoom: 16}}>
                            {chargers.map(x =>
                                <div>
                                    <Placemark
                                        options={{iconColor: current != null && current.id === x.id ? 'blue' : 'black'}}
                                        onClick={() => handleSetCurrent(x)} geometry={[x.latitude, x.longitude]}/>
                                </div>
                            )}
                        </Map>
                    </div>
                </YMaps>
            </Grid>
        </Grid>
    )
}

export default ChargesMap;