import axios from "axios";

const ENV = "DEV";

const getUrl = (ENV) => {
    switch (ENV) {
        case "DEV":
            return "https://localhost:7097/"
        case "PROD":
            return "http://172.19.26.205:88/"
    }
}

const BASE_URL = getUrl(ENV);



const instance = axios.create({
    baseURL: BASE_URL,
    withCredentials: true
})

export const authAPI = {
    me() {
        return instance.get(`auth`).then(response => response.data.data);
    }
}

export const chargersAPI = {
    getChargers() {
        return instance.get(`chargers`).then(response => response.data)
    }
}
